.Resume {
  text-align: center;
}

.card-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.card {
  width: 300px;
  overflow: hidden;
  box-shadow: 0px 0px 15px -5px;
  color: black;
}

.card img {
  height: 200px;
  object-fit: cover;
  width: 100%;
}

.card-content {
  margin: 1rem;
}